body {
  font-family: "Poppins", sans-serif;
  margin: 0px;
  background-color: rgb(118, 189, 156);
}
.note {
  background-color: rgb(243, 243, 122);
  border-radius: 10px;
  padding: 1rem;
  min-height: 170px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  white-space: pre-wrap;
  word-wrap: break-word;
}
.note.new {
  background-color: rgb(131, 226, 230);
}
textarea {
  background-color: rgb(131, 226, 230);
  resize: none;
  border: none;
  font-family: "Poppins", sans-serif;
}
textarea:focus {
  outline: none;
}
.note-footer {
  display: flex;
  justify-content: space-between;
}
.notes-list {
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}
.container {
  margin: auto;
  max-width: 1000px;
  padding: 0px 15px;
  min-height: 100vh;
}
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.save {
  border: none;
  background-color: rgb(127, 195, 197);
  padding: 5px 10px;
  border-radius: 15px;
  font-family: "Poppins", sans-serif;
}
.save:hover {
  background-color: rgb(78, 182, 185);
  cursor: pointer;
}
.delete-icon {
  cursor: pointer;
}
.delete-icon:hover {
  color: coral;
}
h1 {
  color: rgb(243, 243, 122);
}
.search {
  display: flex;
  align-items: center;
  background-color: rgb(122, 226, 230);
  border-radius: 10px;
  padding: 10px;
  margin: 10px 0px;
  font-size: 30px;
}
input {
  outline: none;
  background-color: rgb(122, 226, 230);
  border: none;
  width: 100%;
  font-size: large;
  font-family: "Poppins", sans-serif;
}
.switch-mode {
  border: none;
  background-color: rgb(90, 170, 90);
  padding: 5px 10px;
  border-radius: 15px;
  color: white;
  font-family: "Poppins", sans-serif;
}
.switch-mode:hover {
  background-color: rgb(30, 104, 30);
  cursor: pointer;
}
.dark-mode {
  background-color: black;
}
.dark-mode h1 {
  color: rgb(122, 226, 230);
}
